.statistics {
    width: 50%;
    margin: 0 auto 50px;
    background-color: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 1), -23px 0 20px -23px rgba(0, 0, 0, 0.8),
        23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.title {
    text-align: center;
    padding: 30px 0;
    text-transform: uppercase;
    color: rgb(138, 138, 138);
}

.statList {
    display: flex;
    width: 100%;
}

.item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 0;

    width: calc(100% / 4);
    color: #fff;
}

.item:first-child {
    background-color: rgb(131, 175, 219);
}

.item:nth-child(2) {
    background-color: rgb(195, 98, 195);
}

.item:nth-child(3) {
    background-color: rgb(230, 108, 108);
}

.item:nth-child(4) {
    background-color: rgb(20, 172, 94);
}
.item:last-child {
    background-color: rgb(0, 219, 219);
}

.label {
    font-size: 16px;
    margin-bottom: 5px;
}

.percentage {
    font-size: 24px;
}